if (props.parentType === "module") {
    onTypeSelect("model");
}
onMounted(() => {
    switch (props.createType) {
        case "Screen":
            isModule.value = false;
            isEmptyPage.value = true;
            isModel.value = true;
            isScreen.value = true;
            isEntity.value = isProcessDiagram.value = isProcess.value = isFlow.value = false;
            createButtonVisibility.value = true;
            isQuickComponent.value = isQuickTemplate.value = isImportFromFile.value = isGenerateFromEntity.value = false;
            break;
        case "Process Diagram":
            (isProcess.value = false), (isProcessDiagram.value = true), (isEntity.value = isScreen.value = isFlow.value = false);
            createButtonVisibility.value = true;
            break;
