            isFolder.value = isProcessDiagram.value = isModel.value = false;
            setAllSubRadiosFalse();
            createButtonVisibility.value = true;
            break;
    }
});

function create() {
    executor.execute(
        async () => {
            if (isModule.value) {
                await executor.executeUseCase<ICreateModule>(ICreateModule, {
                    name: name.value,
                    applicationID: props.parentId,
                    description: description.value,
                });
                emit("close");
            } else if (isFolder.value) {
                return;
            } else if (isModel.value) {
                let modelBody: Array<IModelBodyObject>;
                let createObject: ICreateModelParam;
                if (isEntity.value) {
                    modelBody = [{ key: "entityDesigner", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "entityDesigner",
                        modelAdditionals: {},
                    };
                } else if (isEmptyPage.value) {
                    modelBody = [{ key: "qjson", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "qjson",
                        modelAdditionals: { qjsonType: "qjson" },
                    };
                } else if (isQuickComponent.value) {
                    modelBody = [{ key: "qjson", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "qjson",
                        modelAdditionals: { qjsonType: "quickComponent" },
                    };
                } else if (isQuickTemplate.value) {
                    modelBody = [{ key: "qjson", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "qjson",
                        modelAdditionals: { qjsonType: "template" },
                    };
                } else if (isImportFromFile.value) {
                    console.log("isImportFromFile");
                    return;
                } else if (isGenerateFromEntity.value) {
                    console.log("isGenerateFromEntity");
                    return;
                } else if (isProcess.value) {
                    const model = processEditorUtil.getEmptyProcessWizardModel(props.parentName);
                    switch (processFlow.value.type) {
                        case "create-new-process-flow":
                            model.usingExistingBpmnFile = false;
                            model.selectedExistingBpmnFileID = "";
                            model.processFlowName = processFlow.value.newProcessFlowName || name.value;
                            break;
                        case "choose-existing-process-flow":
                            model.usingExistingBpmnFile = true;
                            model.selectedExistingBpmnFileID = processFlow.value.selectedExistingBpmnFileID || "";
                            break;
                        default:
                            break;
                    }
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelType: "process",
                        modelBody: [{ key: "process", model }],
                        modelAdditionals: {},
                    };
                } else if (isProcessDiagram.value) {
                    modelBody = [{ key: "bpmn", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "bpmn",
                        modelAdditionals: { isAutoGenerated: false, isModifiedByUser: false },
                    };
                } else if(isFlow.value){
                    modelBody = [{ key: "flow", model: "" }];
                    createObject = {
                        name: name.value,
                        parentObjectId: props.parentId,
                        modelBody,
                        modelType: "flow",
                        modelAdditionals: {},
                    };
                } else {
                    throw createError({ message: "unexpected case", type: "technical" });
                }
                if (props.duplicateFrom) {
                    createObject.duplicateFrom = props.duplicateFrom;
                }
                const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);
                await onAfterModelCreated(createdModel);
                emit("close");
            }
        },
        { loading: true }
    );
}

async function onAfterModelCreated(createdModel: IModel) {
    await studio.checkout(createdModel);
    studio.openItem(createdModel);
}
