function handleCreateNewProcessFlow() {
    switch (processFlow.value.type) {
        case "create-new-process-flow":
            processFlow.value.selectedExistingBpmnFileID = "";
            processFlow.value.selectedExistingBpmnFileName = "";
            processFlow.value.newProcessFlowNameRenderKey = CryptoHelper.CreateGuid();
            setNewProcessFlowNameValue(name.value);
            break;
        case "choose-existing-process-flow":
            processFlow.value.isNewProcessFlowNameValid = false;
            setNewProcessFlowNameValue("");
            break;
        default:
            break;
    }
}

function setDescValue(desc: string) {
    description.value = desc;
}

function handleSelectedExistingBpmnFileID($event: any) {
    let selectedExistingBpmnFile = $event.target[$event.target.selectedIndex];
    processFlow.value.selectedExistingBpmnFileID = selectedExistingBpmnFile.id;
}

