___CSS_LOADER_EXPORT___.push([module.id, "\n.createButtonDiv[data-v-181271f8] {\n    text-align: end;\n    padding-top: 21px;\n    height: 53px;\n    border-top: 1px solid #e0e0e0;\n    margin-top: 32px;\n    margin-left: 20px;\n    margin-right: 20px;\n    margin-bottom: 16px;\n}\n.createButton[data-v-181271f8] {\n    background-color: #151233;\n    color: white;\n    border-radius: 4px;\n    border: none;\n    width: 74px;\n    height: 32px;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0.01px;\n    opacity: 1;\n}\n.nameInput[data-v-181271f8] {\n    width: 100%;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    height: 40px;\n    opacity: 1;\n    outline: none;\n}\n[data-v-181271f8]::placeholder {\n    padding-left: 10px;\n    padding-top: 5px;\n    color: #a1a1a1;\n    font-style: normal;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    font-size: 14px;\n    opacity: 1px;\n}\n.nameInputDiv[data-v-181271f8] {\n    padding-left: 20px;\n    padding-right: 20px;\n    padding-top: 18px;\n}\n.duplicateNoteDiv[data-v-181271f8] {\n    margin-left: 20px;\n    margin-right: 20px;\n    margin-top: 18px;\n}\n.labelValue[data-v-181271f8] {\n    color: #151233;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n}\n.duplicateText[data-v-181271f8] {\n    font-weight: bold;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n}\n.choose-existing-process-flow-dropdown[data-v-181271f8] {\n    width: 511px;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    height: 40px;\n    padding: 8px;\n    font-size: 14px;\n    font-family: \"Roboto\";\n}\n.choose-existing-process-flow-dropdown-label[data-v-181271f8] {\n    color: #151233;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 14px;\n    letter-spacing: 0px;\n    opacity: 1;\n    font-family: \"Roboto\";\n}\n.requiredIcon[data-v-181271f8] {\n    color: #757575;\n    font-size: 15px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/newItem.vue"],"names":[],"mappings":";AAolBA;IACI,eAAe;IACf,iBAAiB;IACjB,YAAY;IACZ,6BAA6B;IAC7B,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;IAClB,mBAAmB;AACvB;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,qBAAqB;IACrB,sBAAsB;IACtB,UAAU;AACd;AAEA;IACI,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,YAAY;IACZ,UAAU;IACV,aAAa;AACjB;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;IACd,kBAAkB;IAClB,qBAAqB;IACrB,mBAAmB;IACnB,eAAe;IACf,YAAY;AAChB;AAEA;IACI,kBAAkB;IAClB,mBAAmB;IACnB,iBAAiB;AACrB;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;AACpB;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,qBAAqB;IACrB,mBAAmB;IACnB,UAAU;AACd;AACA;IACI,iBAAiB;IACjB,eAAe;IACf,qBAAqB;IACrB,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,qBAAqB;AACzB;AACA;IACI,cAAc;IACd,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,mBAAmB;IACnB,UAAU;IACV,qBAAqB;AACzB;AAEA;IACI,cAAc;IACd,eAAe;AACnB","sourcesContent":["<script setup lang=\"ts\">\nimport { onMounted, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { INewItemProps } from \"../../../../domain/presentation/dialogs/INewItemProps\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { ICreateModel, ICreateModelParam } from \"../../../../domain/useCase/ICreateModel\";\nimport { ICreateModule } from \"../../../../domain/useCase/ICreateModule\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { IStudio } from \"../../../../domain/useCase/IStudio\";\nimport { IModel, IModelBodyObject, IModule } from \"../../../../domain/model/models\";\nimport { IProcessEditorUtil } from \"../../../../domain/useCase/IProcessEditorUtil\";\nimport { createError } from \"../../../../domain/model/shellError\";\nimport IDEInput from \"../../components/application/IDEInput.vue\";\nimport { IIDEInputDefinitions } from \"../../../../domain/presentation/IIDEInputDefinitions\";\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IBpmnAdditionals } from '@stechquick/algae/lib/qCloudTemp/quickCloud';\n\ninterface INewItemPropsInline extends INewItemProps {\n    parentId: string;\n    parentName: string;\n    parentType: \"folder\" | \"module\" | \"application\";\n    createType: \"Screen\" | \"Process Diagram\" | \"Entity\" | \"Process Wizard\" | \"Module\" | \"Flow\";\n    duplicateFrom?: IModel;\n}\n\ninterface IProcessFlow {\n    type?: \"create-new-process-flow\" | \"choose-existing-process-flow\";\n    newProcessFlowName?: string;\n    newProcessFlowNameRenderKey?: string;\n    selectedExistingBpmnFileID?: string;\n    selectedExistingBpmnFileName: string;\n    isNewProcessFlowNameValid: boolean;\n}\n\nconst props = defineProps<INewItemPropsInline>();\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst bpmnFiles = ref();\nif (props.createType == \"Process Wizard\") {\n    bpmnFiles.value = processEditorUtil.getModelsOfModule(viewModel.studio.items.find((item) => item.ID == props.parentId) as IModule).bpmns\n    .filter(bpmn => (bpmn.additionals as IBpmnAdditionals)?.isAutoGenerated != false && (bpmn.additionals as IBpmnAdditionals)?.isModifiedByUser != true)\n}\nconst emit = defineEmits([\"close\"]);\n\n// module\nconst isDescValid = ref<boolean>(true);\n\n// entity\nconst isNameValid = ref<boolean>(false);\n\nconst validations = ref<Record<\"name\" | \"description\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 40 }, availableCharacters: true, nonSpaceCharacter: true },\n    description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: true, nonSpaceCharacter: false },\n});\n\nconst isModule = ref(false);\nconst isFolder = ref(false);\nconst isModel = ref(false);\nconst name = ref(\"\");\nconst description = ref(\"\");\n\nconst isEntity = ref(false);\nconst isScreen = ref(false);\nconst isProcess = ref(false);\nconst isProcessDiagram = ref(false);\nconst isEmptyPage = ref(false);\nconst isQuickComponent = ref(false);\nconst isQuickTemplate = ref(false);\nconst isImportFromFile = ref(false);\nconst isGenerateFromEntity = ref(false);\nconst isFlow = ref(false);\n\nconst createButtonVisibility = ref(true);\n\nconst processFlow = ref({\n    selectedExistingBpmnFileName: \"\",\n    isNewProcessFlowNameValid: false,\n    newProcessFlowName: \"\",\n    newProcessFlowNameRenderKey: CryptoHelper.CreateGuid(),\n} as IProcessFlow);\n\nif (props.parentType === \"module\") {\n    onTypeSelect(\"model\");\n}\nonMounted(() => {\n    switch (props.createType) {\n        case \"Screen\":\n            isModule.value = false;\n            isEmptyPage.value = true;\n            isModel.value = true;\n            isScreen.value = true;\n            isEntity.value = isProcessDiagram.value = isProcess.value = isFlow.value = false;\n            createButtonVisibility.value = true;\n            isQuickComponent.value = isQuickTemplate.value = isImportFromFile.value = isGenerateFromEntity.value = false;\n            break;\n        case \"Process Diagram\":\n            (isProcess.value = false), (isProcessDiagram.value = true), (isEntity.value = isScreen.value = isFlow.value = false);\n            createButtonVisibility.value = true;\n            break;\n        case \"Entity\":\n            isEntity.value = true;\n            isScreen.value = isProcessDiagram.value = isProcess.value = isFlow.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"Process Wizard\":\n            isProcess.value = true;\n            isEntity.value = isProcessDiagram.value = isScreen.value = isFlow.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"Flow\":\n            isFlow.value = true;\n            isModule.value = false;\n            isScreen.value = isProcessDiagram.value = isProcess.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"Module\":\n            isModule.value = true;\n            isFolder.value = isProcessDiagram.value = isModel.value = false;\n            setAllSubRadiosFalse();\n            createButtonVisibility.value = true;\n            break;\n    }\n});\n\nfunction create() {\n    executor.execute(\n        async () => {\n            if (isModule.value) {\n                await executor.executeUseCase<ICreateModule>(ICreateModule, {\n                    name: name.value,\n                    applicationID: props.parentId,\n                    description: description.value,\n                });\n                emit(\"close\");\n            } else if (isFolder.value) {\n                return;\n            } else if (isModel.value) {\n                let modelBody: Array<IModelBodyObject>;\n                let createObject: ICreateModelParam;\n                if (isEntity.value) {\n                    modelBody = [{ key: \"entityDesigner\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"entityDesigner\",\n                        modelAdditionals: {},\n                    };\n                } else if (isEmptyPage.value) {\n                    modelBody = [{ key: \"qjson\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"qjson\",\n                        modelAdditionals: { qjsonType: \"qjson\" },\n                    };\n                } else if (isQuickComponent.value) {\n                    modelBody = [{ key: \"qjson\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"qjson\",\n                        modelAdditionals: { qjsonType: \"quickComponent\" },\n                    };\n                } else if (isQuickTemplate.value) {\n                    modelBody = [{ key: \"qjson\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"qjson\",\n                        modelAdditionals: { qjsonType: \"template\" },\n                    };\n                } else if (isImportFromFile.value) {\n                    console.log(\"isImportFromFile\");\n                    return;\n                } else if (isGenerateFromEntity.value) {\n                    console.log(\"isGenerateFromEntity\");\n                    return;\n                } else if (isProcess.value) {\n                    const model = processEditorUtil.getEmptyProcessWizardModel(props.parentName);\n                    switch (processFlow.value.type) {\n                        case \"create-new-process-flow\":\n                            model.usingExistingBpmnFile = false;\n                            model.selectedExistingBpmnFileID = \"\";\n                            model.processFlowName = processFlow.value.newProcessFlowName || name.value;\n                            break;\n                        case \"choose-existing-process-flow\":\n                            model.usingExistingBpmnFile = true;\n                            model.selectedExistingBpmnFileID = processFlow.value.selectedExistingBpmnFileID || \"\";\n                            break;\n                        default:\n                            break;\n                    }\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelType: \"process\",\n                        modelBody: [{ key: \"process\", model }],\n                        modelAdditionals: {},\n                    };\n                } else if (isProcessDiagram.value) {\n                    modelBody = [{ key: \"bpmn\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"bpmn\",\n                        modelAdditionals: { isAutoGenerated: false, isModifiedByUser: false },\n                    };\n                } else if(isFlow.value){\n                    modelBody = [{ key: \"flow\", model: \"\" }];\n                    createObject = {\n                        name: name.value,\n                        parentObjectId: props.parentId,\n                        modelBody,\n                        modelType: \"flow\",\n                        modelAdditionals: {},\n                    };\n                } else {\n                    throw createError({ message: \"unexpected case\", type: \"technical\" });\n                }\n                if (props.duplicateFrom) {\n                    createObject.duplicateFrom = props.duplicateFrom;\n                }\n                const createdModel = await executor.executeUseCase<ICreateModel>(ICreateModel, createObject);\n                await onAfterModelCreated(createdModel);\n                emit(\"close\");\n            }\n        },\n        { loading: true }\n    );\n}\n\nasync function onAfterModelCreated(createdModel: IModel) {\n    await studio.checkout(createdModel);\n    studio.openItem(createdModel);\n}\n// not: listHistories dialog componentı olacak.\n// örnek modelİd = vgvrxgap-ahvp-igjc-h2ft-w33p9q41 asd.qjson\n\n// parentObjectId = objectId\n// this.executor.executeUseCase<ICreateModel>(ICreateModel, { name: \"model1\", parentObjectId: createdParentObjectId!, modelType: \"qjson\", modelAdditionals: { qjsonType: \"qjson\" } });\n\nfunction setAllSubRadiosFalse() {\n    isEntity.value = isScreen.value = false;\n    isEmptyPage.value =\n        isQuickComponent.value =\n        isQuickTemplate.value =\n        isImportFromFile.value =\n        isGenerateFromEntity.value =\n        createButtonVisibility.value =\n            false;\n}\n\nfunction onTypeSelect(\n    type:\n        | \"module\"\n        | \"folder\"\n        | \"model\"\n        | \"entity\"\n        | \"screen\"\n        | \"process\"\n        | \"emptyPage\"\n        | \"quickComponent\"\n        | \"quickTemplate\"\n        | \"importFromFile\"\n        | \"generateFromEntity\"\n        | \"flow\"\n) {\n    switch (type) {\n        case \"module\":\n            isModule.value = true;\n            isFolder.value = isModel.value = false;\n            setAllSubRadiosFalse();\n            createButtonVisibility.value = true;\n            break;\n        case \"folder\":\n            isFolder.value = true;\n            isModule.value = isModel.value = false;\n            setAllSubRadiosFalse();\n            break;\n        case \"model\":\n            isModel.value = true;\n            isModule.value = isFolder.value = false;\n            setAllSubRadiosFalse();\n            break;\n        case \"entity\":\n            isEntity.value = true;\n            isScreen.value = isProcess.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"flow\":\n            isFlow.value = true;\n            isScreen.value = isProcess.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"screen\":\n            isModule.value = false;\n            isModel.value = true;\n            isScreen.value = true;\n            isEntity.value = isProcess.value = false;\n            createButtonVisibility.value = false;\n            break;\n        case \"process\":\n            isProcess.value = true;\n            isEntity.value = isScreen.value = false;\n            createButtonVisibility.value = true;\n            break; // openProcessWizard(); emit(\"close\"); break;\n        case \"emptyPage\":\n            isEmptyPage.value = true;\n            isQuickComponent.value = isQuickTemplate.value = isImportFromFile.value = isGenerateFromEntity.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"quickComponent\":\n            isQuickComponent.value = true;\n            isEmptyPage.value = isQuickTemplate.value = isImportFromFile.value = isGenerateFromEntity.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"quickTemplate\":\n            isQuickTemplate.value = true;\n            isEmptyPage.value = isQuickComponent.value = isImportFromFile.value = isGenerateFromEntity.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"importFromFile\":\n            isImportFromFile.value = true;\n            isEmptyPage.value = isQuickComponent.value = isQuickTemplate.value = isGenerateFromEntity.value = false;\n            createButtonVisibility.value = true;\n            break;\n        case \"generateFromEntity\":\n            isGenerateFromEntity.value = true;\n            isEmptyPage.value = isQuickComponent.value = isQuickTemplate.value = isImportFromFile.value = false;\n            createButtonVisibility.value = true;\n            break;\n    }\n}\n\nfunction upload(event: Event) {\n    // file = event.target.files[0];\n    // let formData = new FormData();\n    // formData.append('file', file);\n}\n\nfunction handleCreateNewProcessFlow() {\n    switch (processFlow.value.type) {\n        case \"create-new-process-flow\":\n            processFlow.value.selectedExistingBpmnFileID = \"\";\n            processFlow.value.selectedExistingBpmnFileName = \"\";\n            processFlow.value.newProcessFlowNameRenderKey = CryptoHelper.CreateGuid();\n            setNewProcessFlowNameValue(name.value);\n            break;\n        case \"choose-existing-process-flow\":\n            processFlow.value.isNewProcessFlowNameValid = false;\n            setNewProcessFlowNameValue(\"\");\n            break;\n        default:\n            break;\n    }\n}\n\nfunction setDescValue(desc: string) {\n    description.value = desc;\n}\n\nfunction handleSelectedExistingBpmnFileID($event: any) {\n    let selectedExistingBpmnFile = $event.target[$event.target.selectedIndex];\n    processFlow.value.selectedExistingBpmnFileID = selectedExistingBpmnFile.id;\n}\n\n// general validations methods\nfunction setDescValidationStatus(isValid: boolean) {\n    isDescValid.value = isValid;\n}\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNewProcessFlowNameValue(value: string) {\n    processFlow.value.newProcessFlowName = value;\n}\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\nfunction setNewProcessFlowNameValidationStatus(isValid: boolean) {\n    processFlow.value.isNewProcessFlowNameValid = isValid;\n}\n\n</script>\n\n<template>\n    <div>\n        <div></div>\n\n        <!-- New Module -->\n\n        <div class=\"nameInputDiv\" v-if=\"isModule\">\n            <div>\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Give your ${props.createType} a name `\"\n                    :type=\"'text'\"\n                    info-text=\"Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n            <div style=\"margin-top: 15px\">\n                <IDEInput\n                    :isRequired=\"false\"\n                    @getValue=\"setDescValue\"\n                    @validateStatus=\"setDescValidationStatus\"\n                    :placeholder=\"'Description'\"\n                    :type=\"'textarea'\"\n                    info-text=\"Description\"\n                    :validations=\"validations.description\"\n                />\n            </div>\n        </div>\n\n        <div v-if=\"props.duplicateFrom\" class=\"duplicateNoteDiv\">\n            <span class=\"labelValue\"\n                >* This new Item will be duplicated from <span class=\"duplicateText\">{{ props.duplicateFrom.name }} </span></span\n            >\n        </div>\n        <!-- New Entity -->\n        <div class=\"nameInputDiv\" v-if=\"isEntity\">\n            <div>\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Give your ${props.createType} a name `\"\n                    :type=\"'text'\"\n                    info-text=\"Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n        </div>\n\n        <!-- New Flow -->\n        <div class=\"nameInputDiv\" v-if=\"isFlow\">\n            <div>\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Give your ${props.createType} a name `\"\n                    :type=\"'text'\"\n                    info-text=\"Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n        </div>\n\n        <!-- New Process -->\n        <div class=\"nameInputDiv\" v-if=\"isProcess\">\n            <div>\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Name`\"\n                    :type=\"'text'\"\n                    info-text=\"Process Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n            <hr />\n            <div>\n                <input\n                    type=\"radio\"\n                    id=\"create-new-process-flow\"\n                    name=\"create-new-process-flow\"\n                    value=\"create-new-process-flow\"\n                    v-model=\"processFlow.type\"\n                    @change=\"handleCreateNewProcessFlow\"\n                />Create New Process Flow (BPMN)\n\n                <div v-if=\"processFlow.type == 'create-new-process-flow'\">\n                    <IDEInput\n                        :isRequired=\"true\"\n                        @getValue=\"setNewProcessFlowNameValue\"\n                        @validateStatus=\"setNewProcessFlowNameValidationStatus\"\n                        :key=\"processFlow.newProcessFlowNameRenderKey\"\n                        :value=\"processFlow.newProcessFlowName\"\n                        :placeholder=\"`Name`\"\n                        :type=\"'text'\"\n                        info-text=\"Process Flow Name\"\n                        :validations=\"validations.name\"\n                    />\n                </div>\n\n                <br />\n\n                <input\n                    type=\"radio\"\n                    id=\"create-new-process-flow\"\n                    name=\"choose-existing-process-flow\"\n                    value=\"choose-existing-process-flow\"\n                    v-model=\"processFlow.type\"\n                    @change=\"handleCreateNewProcessFlow\"\n                />Choose Existing Process Flow (BPMN)\n\n                <div v-if=\"processFlow.type == 'choose-existing-process-flow'\">\n                    <span class=\"choose-existing-process-flow-dropdown-label\">BPMN File</span\n                    ><i v-if=\"processFlow.type == 'choose-existing-process-flow'\" class=\"requiredIcon\">*</i>\n                    <select\n                        class=\"choose-existing-process-flow-dropdown\"\n                        name=\"choose-existing-process-flow\"\n                        id=\"choose-existing-process-flow\"\n                        v-model=\"processFlow.selectedExistingBpmnFileName\"\n                        @change=\"handleSelectedExistingBpmnFileID\"\n                        required\n                    >\n                        <option value=\"\" disabled selected>Choose Existing Process Flow (BPMN)</option>\n                        <option v-for=\"bpmnFile in bpmnFiles\" :id=\"bpmnFile.ID\">\n                            {{ bpmnFile.name }}\n                        </option>\n                    </select>\n                </div>\n            </div>\n        </div>\n\n        <div class=\"nameInputDiv\" v-if=\"isProcessDiagram\">\n            <div>\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Give your ${props.createType} a name `\"\n                    :type=\"'text'\"\n                    info-text=\"Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n        </div>\n\n        <div v-if=\"isScreen\">\n            <div class=\"nameInputDiv\" v-if=\"isEmptyPage || isQuickComponent || isQuickTemplate\">\n                <IDEInput\n                    :isRequired=\"true\"\n                    @getValue=\"setNameValue\"\n                    @validateStatus=\"setNameValidationStatus\"\n                    :placeholder=\"`Give your ${props.createType} a name `\"\n                    :type=\"'text'\"\n                    info-text=\"Name\"\n                    :validations=\"validations.name\"\n                />\n            </div>\n            <div class=\"nameInputDiv\" v-if=\"isImportFromFile\">\n                <span class=\"labelValue\">Name</span>\n                <input class=\"nameInput\" v-model=\"name\" :placeholder=\"`Give your ${props.createType} a name `\" />\n                <div>\n                    <input type=\"file\" @change=\"upload($event)\" accept=\".qjson\" />\n                </div>\n            </div>\n        </div>\n\n        <!-- New Folder -->\n        <div v-if=\"isFolder\"></div>\n\n        <div class=\"createButtonDiv\">\n            <button\n                :style=\"{\n                    opacity: isProcess\n                        ? !(isNameValid && isDescValid) ||\n                          !(!!processFlow.selectedExistingBpmnFileName.length || processFlow.isNewProcessFlowNameValid)\n                            ? '0.5'\n                            : ''\n                        : !(isNameValid && isDescValid)\n                        ? '0.5'\n                        : '',\n                }\"\n                class=\"createButton\"\n                @click=\"create()\"\n                :disabled=\"\n                    isProcess\n                        ? !(isNameValid && isDescValid) ||\n                          !(!!processFlow.selectedExistingBpmnFileName.length || processFlow.isNewProcessFlowNameValid)\n                        : !(isNameValid && isDescValid)\n                \"\n            >\n                {{ props.duplicateFrom ? \"DUPLICATE\" : \"CREATE\" }}\n            </button>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n.createButtonDiv {\n    text-align: end;\n    padding-top: 21px;\n    height: 53px;\n    border-top: 1px solid #e0